<template>
  <div class="home">
    <v-container class="pa-8 mt-3">
      <v-row class="mb-6" justify="center" align="center" v-for="items in list_button" :key="items" @click="Open_event(items)">
        <v-col align="center">
          <v-card
            color="red"
            :style="items.style"
          >
            <v-row class="" justify="center" align="center">
              <v-col align="left" class="" cols="8">
                <v-card-title class="white--text">
                  {{items.title}}
                </v-card-title>
              </v-col>
              <v-col cols="4">
                <v-btn depressed fab elevation="0" :color="items.color_button">
                  <v-icon color="white">{{items.icon}}</v-icon>
                </v-btn>
              </v-col>
            </v-row>
          <v-divider class="ma-4 white"></v-divider>
            <v-card-text
              class="white--text pt-0 pb-7"
              style="text-align-last: left;"
            >
            {{items.text}}
            </v-card-text>
          </v-card>


        </v-col>
      </v-row>

    </v-container>
  </div>
</template>

<script>
import Swal from "sweetalert2";
export default {
  name: "HomePage",
  components: {},
  data: () => ({

    list_button:[
      {
       title:"Booking" , text:"Booking Meeting room" , icon : "mdi-calendar-month",color_button: "light-blue lighten-3" , style:"border-radius: 15px; background-image: radial-gradient( circle 465px at -15.1% -25%,  rgba(17,130,193,1) 0%, rgba(67,166,238,1) 49%, rgba(126,203,244,1) 90.2% );"
      },
      {
       title:"Join" , text:"Join Meeting room" , icon : "mdi-account-supervisor",color_button: "pink accent-1" , style:"border-radius: 15px;  background-image: linear-gradient( 143.3deg,  rgba(216,27,96,1) 33.1%, rgba(237,107,154,1) 74.9% );"
      },
      {
       title:"Schedule" , text:"Check schedule username" , icon : "mdi-calendar-text",color_button: "purple accent-2" , style:"border-radius: 15px; background-image: radial-gradient( circle farthest-corner at 14.2% 27.5%,  rgba(181,126,285,1) 90%, rgba(181,126,240,1) 0% );"
      }
       
    ],
    loading: false,
    user_login: "",
   
  }),
  mounted() {

  },
  computed: {},
  created() {
    this.get_user_login();
  },
  methods: {
    Open_event(data_click){
      console.log(data_click);
      if(data_click.title === "Booking"){
           this.$router.push("/CalendarMeeting");
      }
      else  if(data_click.title === "Join"){
           this.$router.push("/JoinCalendarMeeting");
      }
      else  if(data_click.title === "Schedule"){
           this.$router.push("/SchedulePerson");
      }
      else{
         Swal.fire({
          icon: "error",
          text: "Someting wrong!",
        });
      }
      
    },
    get_user_login() {
      let data = JSON.parse(
        window.localStorage.getItem("user_login_oneaccess")
      );
      // console.log(data)
      if (data === null) {
        this.user_login = "";
      } else {
        this.user_login = data;
      }
    },
  },
};
</script>
